import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Image from 'react-bootstrap/esm/Image';
import Table from 'react-bootstrap/esm/Table';
import basoc from '../image/basoc.webp';
import { useState } from 'react';
export const Moddal = (props) => {
    const { show, handlecole } = props;
    const [add, setadd] = useState(1);
    // add
    let count = add;
    const handle = () => {
        count++;
        setadd(count)


    }
    const handles = () => {
        count--;
        setadd(count);
    }

    const handleModal = () => {

    }
    //total
    return (
        <>
            <Modal
                show={show}
                onHide={handlecole}
                backdrop="static"
                keyboard={false}

            >
                <Modal.Header closeButton>
                    <Modal.Title>Sản Phẩm</Modal.Title>
                </Modal.Header>

                <Modal.Body>
                    <Table>
                        <thead>
                            <tr>
                                <th>Sản Phẩm</th>
                                <th>Giá</th>
                                <th>SL</th>
                                <th>Action</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>
                                    <Image src={basoc} className='img' />
                                </td>
                                <td><span><b id="price" >300.000</b></span></td>
                                <td>
                                    <button className='btn'>
                                        {add}
                                        <span className='caret'>
                                            <i onClick={handle} className="fa-solid fa-caret-up"></i>
                                            <i onClick={() => handles()} className="fa-solid fa-caret-down"></i>
                                        </span>

                                    </button>
                                </td>
                                <td><button className='btn btn-secondary' type="">Edit</button></td>
                            </tr>
                        </tbody>

                    </Table>

                    <div>
                        <b s>Total:</b>
                    </div>
                </Modal.Body>

                <Modal.Footer>
                    <Button variant="secondary" onClick={handlecole} >
                        Close
                    </Button>
                    <Button variant="primary" onClick={() => handleModal()} >
                        Mua Ngay
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}